import React from 'react'

import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { Button, Checkbox, Form, Input,Select  } from 'antd';
const { TextArea } = Input;
const { Option } = Select;
const Contact = () => {
    
  const prefixSelector = (
    <Form.Item name="prefix" noStyle>
      <Select
        style={{
          width: 70,
        }}
      >
        <Option value="91">+91</Option>
        <Option value="87">+87</Option>
      </Select>
    </Form.Item>
  );


    const onFinish = (values) => {
        console.log('Received values of form: ', values);
      };

  return (
    <div id="contact" className="block contactBlock" style={{paddingTop:'0px'}}>
      <div className="container-fluid">
        <div className="titleHolder">
          <h2>Get in Touch</h2>
          <p>Dolore nam rerum obcaecati fugit odio nobis Molestiae rerum</p>
        </div>

        <Form
          name="normal_login"
          className="login-form"
          initialValues={{
          remember: true,
      }}
      onFinish={onFinish}
    >
      <Form.Item
        name="username"
        rules={[
          {
            required: true,
            message: 'Please input your Fullname!',
          },
        ]}
      >
        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Username" />
       </Form.Item>
       {/* Email Address */}
       <Form.Item
        name="Email"
        rules={[
          {
            required: true,
            message: 'Please Enter Email!',
          },
        ]}
      >
        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Email Address" />
       </Form.Item>


      <Form.Item
        name="password"
        rules={[
          {
            required: true,
            message: 'Please input your Password!',
          },
        ]}
      >
        <Input.Password 
          prefix={<LockOutlined className="site-form-item-icon" />}
          type="password"
          placeholder="Password"
        />
        
      </Form.Item>

      
      <Form.Item name="gender"  rules={[{ required: true }]}>
        <Select
          placeholder="Select Gender"
          
          allowClear
        >
          <Option value="male">male</Option>
          <Option value="female">female</Option>
     
        </Select>
      </Form.Item>
    
      <Form.Item
        name="phone"
        maxLength={10} 
        rules={[
          {
            required: true,
            message: 'Please input your phone number!',
          },
        ]}
      >
        <Input
          addonBefore={prefixSelector}
          style={{
            width: '100%',
          }}
        />
      </Form.Item>
       
      <Form.Item placeholder="Enter Message"  hasFeedback help="Should have something">
      <Input.TextArea allowClear showCount />
    </Form.Item>

      <Form.Item  style={{textAlign:'center'}}>
        <Button type="primary" htmlType="submit" className="login-form-button">
         Submit
        </Button>
      
      </Form.Item>
    </Form>
        </div>
        </div>
  )
}

export default Contact
